    }
  },
  methods: {
    init() {},
    click() {
      /** TODO 后期制作动画效果 **/
      if (this.height === 'auto' && !this.isHide ) {

      }
      this.isHide = !this.isHide;
    }
  },
  computed: {
    ...mapState([
