import PageInfo from '../script/common/PageInfo';
import {mapState} from 'vuex';
import BaseComponent from "../script/common/BaseComponent";

export default {
  name: "InfoTable",
  extends: BaseComponent,
  props: {
    /**
     * example:   key 值与 tableStyle 中的prop对应
     * {
        id:1,
        user_name: 'xxx',
        user_dec: '111',
        state: 1,
        action: ''
      }
     *
     **/
    records: {
      type:Array,
      required: true
    },
    /**
     * example
     * [
     {label: 'ID', prop: 'id', width: ''},
     {label: '角色名称', prop: 'user_name', width: ''},
     {label: '角色描述', prop: 'user_dec', width: ''},
     {label: '状态', prop: 'state', width: ''},
     {label: '操作', prop: 'action', width: ''}
     ]
     **/
    tableStyle: {
      type: Array,
      required: true
    },
    /** 查询调用接口 **/
    /** 页码数据 **/
    pageInfo: {
      type: PageInfo
    },
    /** 是否隐藏页面 default false **/
    hidePage: {
      type: Boolean,
      default(){
        return false;
      }
    }
  },
  computed: {
    ...mapState(['isPhone'])
  },
  data() {
    return {
      cur_page: 1
    }
  },
  methods: {
    handlerSizeChange(val){
      this.$emit('search', val);
    },
    handlerCurrentChange(val){
      /** TODO 校验 **/
      this.$emit('search', val);
    },
    refresh_page() {
      let $this = this;
      this.$nextTick(()=>{
        $this.cur_page = $this.pageInfo.page;
      })
    }
  },
  watch: {
    pageInfo: {
      handler(newVal, oldVal) {
        this.cur_page = newVal.page;
      },
      deep: true
    }
  },
  mounted() {
    if (!this.hidePage)
      this.cur_page = this.pageInfo.page;
  }
}



