import {mapState} from 'vuex';
import {Fragment} from 'vue-fragment'
import BaseComponent from "../script/common/BaseComponent";

export default {
  name: "InfoTableItem",
  extends: BaseComponent,
  components: {Fragment},
  props: {
    tableStyle: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      isHide: false,
      show: false,
      hasSlots: false,
      obj: {
        text: "1111"
      },
      scope: {}
    }
  },
  methods: {},
  computed: {
    ...mapState(['isPhone'])
  },
  created() {

  },
  mounted() {
    let $this = this;
    // TODO 处理界面刷新有问题
    this.$nextTick(() => {
      $this.show = true;
      $this.$forceUpdate();
    });
  },
}



